@font-face {
  font-family: 'SDSamliphopangche_Outline';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts-20-12@1.0/SDSamliphopangche_Outline.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

.App {
  text-align: center;
}

* {
 margin-top: 0px;
 margin-bottom: 0px; 
}

div {
  box-sizing: border-box;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-block-end: 2px solid #aabbcc;
}

.Header-Container {
  background-color: #123456;
  padding-top: 15px;
  padding-bottom: 15px;
}

.Header-Title {
  font-family:"SDSamliphopangche_Outline";
  display:flex;
  color: #abcdef;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 20px;
  letter-spacing: 2px;
  justify-content: space-between;
  align-items: center;
  
}

.Header-Logo {
  height: 32px;
  width: 128px;
  color: #abcdef;
}

.Post-Container {
  display: flex;
  text-align: left;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #112233;
  justify-content: space-between;
  align-items: center;
}

.Post-Title {
  padding-bottom: 8px;
}

.Post-Date {
  padding-bottom: 4px;
}

.Post-Fire {
  font-size: 24px;
}

.Modal-Container {
  font-size: 12px;
  margin-top: 20px;
  padding: 20px;
  background: #eee;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  bottom: 0;
  position:fixed;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}